.EmployeeCard__employee {
    background-color: rgba(255, 255, 255, 0.788);
    text-align: center;
    width: 270px;
    height: 60vh;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 0 50px;
    margin: 8% 15%;
}

.EmployeeCard__employees {
    width: 100%;
    flex: 1;
    display: flex;
    overflow-x: auto;
}

@media screen and (min-width: 700px) {
    .EmployeeCard__employees {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        width: 90vw;
        margin-left: 5%;
    }   
    
    .EmployeeCard__employee {
        width: 230px;
        height: 40vh;
        margin: 5%;
        padding: 0;
    }
}